

export const url = 'https://quranacademy.black-squares.com/graphql';

export const SearchQuery = `
query($name :ShortText  $gender : Boolean $country_id:Int  
    $Spoken_Language_User : [Int!]  $courses_types_id : Int $price_from : Int 
    $price_to : Int $rate_minutes_type_id : Int){
    InstructorSearch(data: {
        name : $name
        gender : $gender
        country_id : $country_id
        Spoken_Language_User : $Spoken_Language_User
        courses_types_id : $courses_types_id 
        price_from : $price_from
        price_to : $price_to
        rate_minutes_type_id : $rate_minutes_type_id
    }, first: 100, page: 1){
        data{
            id
            profile_image{url}
            first_name
            last_name
            courses_type{name}
            country{flag name}
        }
    }
    }
`
export const COUNTRIES =  `
    query{
        countries{
        id
        flag
        name
        }
    }`
export const LANGUAGE = `
    query{
        languageTypes{
        id
        context
        name
        created_at
        updated_at
        }
    }`
export const COURSE_TYPE = `
    query{
        courseType{
        id
        context
        name
        created_at
        updated_at
        }
    }
`
export const DURATION = `
query{
    rateMinutesTypes{
      id
      context
      name
      created_at
      updated_at
    }
  }`
export const USER_TYPES = `
    query{
        meStudents{
        id
        email
        first_name
        last_name
        gender
        need_password
        date_of_birth
        profile_image{url}
        }
    }
`;
