import React from 'react';
import {Link} from 'react-router-dom';

// styles and images
import { Navbar, Nav , Dropdown} from 'react-bootstrap';
import {Avatar} from '@material-ui/core'
import logoWeb from '../../../images/logo/logo-1.svg';
import EditIcon from '@material-ui/icons/Edit';
import SettingsIcon from '@material-ui/icons/Settings';
import HelpIcon from '@material-ui/icons/Help';
import LanguageIcon from '@material-ui/icons/Language';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
// language for test
import text from '../../../languages/english/translate.json';

export default function Header() {
    let data = text?.home.header;
    return (
        <Navbar collapseOnSelect  expand="lg" bg="white" className="shadow" variant="light">
                <Navbar.Brand className="pr-5" href="#intro">
                    <img src={logoWeb} className="img-fluid" alt="logo" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav   className=" mr-auto ">
                            <Link to="/dashboard" className="pr-3 pl-3 nav-link">
                                dashboard
                            </Link>
                            <Link to="/earnings" className="pr-3 pl-3 nav-link">
                                Earnings
                            </Link>
                            <Link to="/sessions" className="pr-3 pl-3 nav-link">
                                Sessions
                            </Link>
                            <Link to="messages" className="pr-3 pl-3 nav-link">
                                Messages
                            </Link>
                            <Link to="notifications" className="pr-3 pl-3 nav-link">
                                Notifications
                            </Link>
                        </Nav>
                        <Nav className="align-items-center">
                            <Nav.Link>
                                <Avatar  alt="Remy Sharp"  />
                            </Nav.Link>
                            <Dropdown>
                                <Dropdown.Toggle  variant="" id="dropdown-basic"> </Dropdown.Toggle>
                                <Dropdown.Menu style={{left : '-135px' , top :'55px'}}>
                                    {/* <Dropdown.Item eventKey="1">Action</Dropdown.Item>
                                    <Dropdown.Item eventKey="2">Another action</Dropdown.Item> */}
                                    <Dropdown.Item eventKey="3" className="pr-3 pl-3">
                                        <Link to='/editProfile'>
                                            <EditIcon className=" mr-2" style={{width : '17px'}}/>
                                            Edit Profile
                                        </Link>
                                    </Dropdown.Item>
                                    <Dropdown.Divider style={{borderTop : '11px solid #F2F2F2'}}/>
                                    <Dropdown.Item  className="pr-3 pl-3">
                                        <Link  
                                        to="/"
                                        onClick={e=>{
                                            localStorage.clear();
                                        }}>
                                                <ExitToAppIcon className="text-danger mr-2" style={{width : '17px'}}/>
                                        Logout</Link>
                                 </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </Nav>

                        
                    </Navbar.Collapse>
        </Navbar>
    )
}