import React , {useEffect} from 'react';
import {Link} from 'react-router-dom';
import { withRouter } from 'react-router';
import { makeStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';

// styles and images
import { Navbar, Nav ,Form , FormControl , Dropdown} from 'react-bootstrap';
import {Avatar} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import logoWeb from '../../../images/logo/logo-1.svg';

// language for test
import text from '../../../languages/english/translate.json';
// search bar style
const useStyles = makeStyles(theme => ({
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: 'rgb(247, 245, 245)',
        border : '1px solid #E2E2E2',
        '&:hover': {
            backgroundColor: 'rgb(247, 245, 245)',
        },
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(1),
            width: 'auto',
        },
    },
    searchIcon: {
        width: theme.spacing(7),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: '#A3A3A3'
    },
    inputRoot: {
        color: '#606060',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 7),
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: 120,
            '&:focus': {
                width: 200,
            },
        },
    },
}));
function Header(props) {

    let data = text?.home.header;
    const classes = useStyles();

    useEffect(() => {
        // handle get user data
        let searchInput =  document.querySelector('#searchInput');
        if(searchInput){
            searchInput.addEventListener('keypress', (e) =>{
                if (e.key === 'Enter') {
                    let searchKey = document.querySelector('#searchInput').value;
                    props.history.push({
                        pathname: '/search',
                        state : searchKey ,
                        search : searchKey
                    });
                    // searchInput.value = '';
                }
            });
        }
    }, []);
    return (
        <Navbar collapseOnSelect  expand="lg" bg="white" className="shadow pr-5 pl-5" variant="light">
                <Navbar.Brand className="pr-5"  >
                    <img src={logoWeb} className="img-fluid" alt="logo" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav   className=" mr-auto ">
                            <Link
                            to='/dashboard'
                                className="pr-3 pl-3 nav-link"
                             >
                                Dashboard
                            </Link>
                            <Link
                                to='/profile'
                                className="pr-3 pl-3 nav-link"
                            >
                                My profile
                            </Link>
                            <Link 
                                to='/message'
                                className="pr-3 pl-3 nav-link"
                            >
                                Messages
                            </Link>
                            <Link
                                to='/notification'
                                className="pr-3 pl-3 nav-link">
                                Notifications
                            </Link>
                        </Nav>
                        <Nav className="align-items-center">
                            <div className={classes.search}>
                                    <div className={classes.searchIcon}>
                                        <SearchIcon />
                                    </div>
                                    <InputBase
                                        placeholder="Search…"
                                        classes={{
                                            root: classes.inputRoot,
                                            input: classes.inputInput,
                                        }}
                                        className="search mr-sm-2"
                                        id="searchInput"
                                        inputProps={{ 'aria-label': 'search' }}
                                    />
                                </div>
                            <Nav.Link>
                                <Avatar  alt="Remy Sharp"  />
                            </Nav.Link>
                            <Dropdown>
                                <Dropdown.Toggle  variant="" id="dropdown-basic">
                                </Dropdown.Toggle>
                                <Dropdown.Menu style={{left : '-135px' , top :'55px'}}>
                                    {/* <Dropdown.Item eventKey="1">Action</Dropdown.Item>
                                    <Dropdown.Item eventKey="2">Another action</Dropdown.Item>
                                    <Dropdown.Item eventKey="3">Something else here</Dropdown.Item>
                                    <Dropdown.Divider /> */}
                                    {/* <Dropdown.Item eventKey="4">Logout</Dropdown.Item> */}
                                    <Dropdown.Item >
                                        <Link  
                                        to="/"
                                        onClick={e=>{
                                            localStorage.clear();
                                        }}>
                                    Logout</Link>
                                 </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                            {/* <Nav.Link>
                                <Link  
                                    to="/"
                                    onClick={e=>{
                                        localStorage.clear();
                                    }}>
                                Logout</Link>
                            </Nav.Link> */}
                        </Nav>
                    </Navbar.Collapse>
        </Navbar>
    )
}
export default  withRouter(Header)